import "./style.css";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { FontLoader } from "three/examples/jsm/loaders/FontLoader.js";
import { TextGeometry } from "three/examples/jsm/geometries/TextGeometry.js";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { GUI } from "dat.gui";

const parameters = {
  index: 1,
  isPaused: false,
  isRunning: false,
  width: window.innerWidth,
  height: window.innerHeight,
  geometryCount: 1000,
  icosahedronGeometry: null,
  padding: 50,
};

// Canvas
const canvas = document.querySelector("canvas.webgl");

// Scene
const scene = new THREE.Scene();

// Camera
const camera = new THREE.PerspectiveCamera(
  75,
  parameters.width / parameters.height,
  0.1,
  1000
);
camera.position.set(0, 0, 15);

// Renderer
const renderer = new THREE.WebGLRenderer({ canvas });
renderer.setSize(parameters.width, parameters.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

// Controls
const controls = new OrbitControls(camera, canvas);
controls.enableDamping = true;
controls.enableZoom = true;
controls.enableRotate = true;

// Lights
const rectAreaLight = new THREE.RectAreaLight(0xb60707, 1, 100, 20);
rectAreaLight.position.z = 10;
scene.add(rectAreaLight);

// Group
const icosahedronGroup = new THREE.Group();
scene.add(icosahedronGroup);

// Font
const fontLoader = new FontLoader();
let font;

// Textures
const textureLoader = new THREE.TextureLoader();
const materialOptions = {
  matcapTexture: textureLoader.load("textures/matcaps/8.png"),
  cometTexture: textureLoader.load("/symbol_02.png"),
  standardMaterial: new THREE.MeshStandardMaterial(),
};

// Text
const textGroup = new THREE.Group();
const textTitleArr = [
  `H E L L O  : )`,
  `A J A Y K S A N T H O S H`,
  `Unleashing Passion through Programming`,
  `Associate Software Developer / UST, Trivandrum`,
  `T H A N K S >_< `,
];
const textSubjectArr = [
  ``,
  `Developer`,
  `Angular | Springboot | Mysql | Bootstrap | MongoDb`,
  `From 15 Sep 21 To Present`,
  "#threejs",
];
const colorArr = [0xb60707, 0x07b6a2, 0x4db607, 0xb66d07, 0xff00f7];

// Load font
fontLoader.load("/fonts/helvetiker_regular.typeface.json", (_font) => {
  font = _font;
  createText(textTitleArr[0], textSubjectArr[0]);
});

// Create icosahedron geometry
function createIcosahedron() {
  icosahedronGroup.clear();

  if (parameters.icosahedronGeometry) {
    parameters.icosahedronGeometry.dispose();
  }

  parameters.icosahedronGeometry = new THREE.IcosahedronGeometry(1, 0);

  for (let index = 0; index < parameters.geometryCount; index++) {
    const icosahedron = new THREE.Mesh(
      parameters.icosahedronGeometry,
      materialOptions.standardMaterial
    );

    icosahedron.position.set(
      (Math.random() - 0.5) * parameters.padding,
      (Math.random() - 0.5) * parameters.padding,
      (Math.random() - 0.5) * parameters.padding
    );
    icosahedron.rotation.set(
      Math.random() * Math.PI,
      Math.random() * Math.PI,
      0
    );

    const scale = Math.random() / 2;
    icosahedron.scale.set(scale, scale, scale);

    icosahedronGroup.add(icosahedron);
  }
}

createIcosahedron();

// Create text
function createText(title, subject) {
  const textGeometry = new TextGeometry(title, {
    font,
    size: 2,
    height: 0.2,
    curveSegments: 12,
    bevelEnabled: false,
    bevelThickness: 0.03,
    bevelSize: 0.02,
    bevelOffset: 0,
    bevelSegments: 5,
  });
  textGeometry.center();
  const text = new THREE.Mesh(textGeometry, materialOptions.standardMaterial);
  text.scale.set(0.3, 0.3, 1);

  const textGeometryDeveloper = new TextGeometry(subject, {
    font,
    size: 0.5,
    height: 0.2,
    curveSegments: 12,
    bevelEnabled: false,
    bevelThickness: 0.03,
    bevelSize: 0.02,
    bevelOffset: 0,
    bevelSegments: 5,
  });
  textGeometryDeveloper.center();
  const developerText = new THREE.Mesh(
    textGeometryDeveloper,
    materialOptions.standardMaterial
  );
  developerText.position.set(3.5, -1.5, 0);

  textGroup.clear();
  textGroup.add(text);
  textGroup.add(developerText);
  scene.add(textGroup);

  textGeometry.dispose();
  textGeometryDeveloper.dispose();
}

// Event listeners
document.addEventListener("dblclick", () => {
  if (!document.webkitFullscreenElement) {
    if (canvas.requestFullScreen) {
      canvas.requestFullScreen();
    } else if (canvas.webkitRequestFullScreen) {
      canvas.webkitRequestFullScreen();
    } else if (canvas.mozRequestFullScreen) {
      canvas.mozRequestFullScreen();
    }
  } else {
    document.webkitExitFullscreen();
  }
});

// Mouse and touch events
let holdTimeout;
const holdDuration = 500; // Define the duration (in milliseconds) for a "hold" event

// Function to handle the "hold" event
function handleHold() {
  parameters.isPaused = true;
}

// Function to start the hold timer
function startHoldTimer() {
  holdTimeout = setTimeout(function () {
    // handleHold();
  }, holdDuration);
}

// Function to clear the hold timer
function clearHoldTimer() {
  if (parameters.isPaused) {
    parameters.isPaused = false;
    animate();
  }

  clearTimeout(holdTimeout);
}

document.addEventListener("mousedown", startHoldTimer);
document.addEventListener("mouseup", clearHoldTimer);
document.addEventListener("touchstart", startHoldTimer);
document.addEventListener("touchend", clearHoldTimer);

window.addEventListener("resize", () => {
  parameters.width = window.innerWidth;
  parameters.height = window.innerHeight;

  camera.aspect = parameters.width / parameters.height;
  camera.updateProjectionMatrix();

  renderer.setSize(parameters.width, parameters.height);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});

// Animation
const clock = new THREE.Clock();

function animate() {
  const elapsedTime = clock.getElapsedTime();

  controls.update();

  icosahedronGroup.children.forEach((donut) => {
    donut.rotation.y += 0.01;
    donut.rotation.x += 0.01;
    donut.position.z += 0.02;

    if (donut.position.z > 15) {
      donut.position.z = Math.random() * -1 * 50;
    }
  });

  camera.position.y = Math.sin(elapsedTime / 3) * 10;
  camera.position.x = Math.tan(elapsedTime / 3);

  if (camera.position.x > 120 && !parameters.isRunning) {
    createText(
      textTitleArr[parameters.index],
      textSubjectArr[parameters.index]
    );
    parameters.isRunning = true;
    rectAreaLight.color.set(colorArr[parameters.index]);

    if (++parameters.index == 5) {
      parameters.index = 0;
    }
  } else if (camera.position.x < 0) {
    parameters.isRunning = false;
  }

  renderer.render(scene, camera);

  if (!parameters.isPaused) {
    requestAnimationFrame(animate);
  }
}

animate();

const gui = new GUI();

const geometryFolder = gui.addFolder("geometries");
geometryFolder
  .add(parameters, "geometryCount")
  .min(500)
  .max(5000)
  .step(10)
  .onFinishChange(createIcosahedron);

geometryFolder
  .add(parameters, "padding")
  .min(10)
  .max(150)
  .step(10)
  .onChange(createIcosahedron);
